html {
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
  height: 100%;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.app {
  text-align: center;
  height: 100%;
}

.app-header {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  min-height: 5%;
  padding: 10px;
  box-sizing: border-box;
  margin-bottom: 10px;
  background-color: #28539E;
  color: white;
  font-size: 1.7rem;
  font-weight: bold;
}

.main-container {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: center;
}

.spinner {
  position: fixed;
  top: 40%;
  left: 45%;
  z-index: 1;
}
.nav-header {
  width: 90%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  background: 'white';
  color: 'black';
  font-weight: bold;
}

.block-link {
  color: black;
  text-decoration: none;
  border-radius: 12px;
  padding: 6px; 
  transition-duration: 0.2s;
  margin: 10px;
  cursor: pointer;
}

.block-link:hover {
  background-color: rgba(40, 83, 158, 0.9);
  color: white;
}

.center {
  width: 50%;
  font-size: 1.3rem;
  order: 2;
}

.prev {
  order: 1;
}

.next {
  order: 3;
}


@media screen and (max-width: 600px) {

  .nav-header {
    flex-wrap: wrap;
  }

  .center {
    width: 100%;
    order: 1;
  }

  .prev {
    order: 2;
  }

  .next {
    order: 3;
  }

}
.modal {
  position: fixed;
  z-index: 1;
  left: 30%;
  top: 20%;
  width: 20%;
  background-color: rgba(0,0,0,0.6); 
  border-radius: 12px;
  padding: 3px;
}

.modal-content {
  background-color: #fefefe;
  border-radius: 10px;
  height: calc(100% - 35px);
  width: calc(100% - 20px);
  padding: 10px;
  padding-top: 25px;
  text-align: justify;
  font-size: 0.8rem;
}

.close {
  color: #aaa;
  position: absolute;
  top: 5px;
  right: 10px;
  font-weight: bold;
  font-size: 1.5rem;
}

.close:hover {
  color: black;
  text-decoration: none;
  cursor: pointer;
}

@media screen and (max-width: 600px) {

  .modal {
    width: 40%;
  }

}
.calendar-grid {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  width: 70%;
  margin: auto;
  margin-bottom: 25px;
}

.week-day-header {
  background: rgba(178, 191, 215, 0.9);
  border: 1px solid black;
  height: 5%;
}

.grid-cell {
  width: calc(12% - 10px);
  padding: 8px;
  font-weight: bold;
}

.calendar-day {
  display: flex;
  position: relative;
  border: 1px solid black;
  padding-top: 6%;
}

.unbooked:hover {
  background-image: url(data:image/png;base64,);
  background-repeat: no-repeat;
  background-color: white;
  background-size: contain;
  cursor: pointer;
}

.booked {
  background-image: url(data:image/png;base64,);
  background-repeat: no-repeat;
  background-size: contain;
  background-color: white;
}

.booked:hover {
  background-image: url(data:image/png;base64,);
  cursor: pointer;
}

.today {
  background: #d7e2f4;
}

.disabled {
  cursor: default;
  font-weight: normal;
  opacity: 0.5;
}

.disabled.booked:hover {
  background-image: url(data:image/png;base64,);
  cursor: default;
}

.sunday {
  color: red;
  opacity: 0.7;
}

.sunday.booked:hover {
  background-image: url(data:image/png;base64,);
  cursor: default;
}


@media screen and (max-width: 600px) {

  .week-day-header {
    display: none;
  }

  .grid-cell {
    width: 60%;
  }
  
  .calendar-day {
    margin: auto;
    padding: 10px;
    display: block;
  }

  .calendar-day-disabled {
    display: none;
  }

}


